.sentdisp-container {
    /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    padding-bottom: 30px;
  }

.sentdisp-container > h1 {
  /* color: #fff; */
  font-size: 100px;
  margin-top: -100px;
}


.sentdisp-container > p {
    margin-top: 8px;
    /* color: #fff; */
    align-items: left;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }

  .sentdisp-data {
    content: "";
    display: table;
    clear: both;
    
      

  }
  
  .legend-title{
    text-align: center;
    font-size: 25px;

  }
  .legend-text {

    text-align: left;
    padding-left: 100px;
    font-size: 20px;
  }


  .sentdisp-btns {
    margin-top: 32px;
  }
  
  .sentdisp-btns .btn {
    margin: 6px;
  }

  .sent-container > p {
    font-size: 30px;
  }

  .btn-mobile{
      display: block;
      text-decoration: none;
  }
  
  .btn {
    width: 100%;
  }